import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';

function Square(props) {
  return (
    <button className="square" onClick={props.onClick}>
      {props.value}
    </button>
  );
}

class Board extends React.Component {
  renderSquare(i) {
    return (
            <Square
              key={i}
              value={this.props.squares[i]}
              onClick={() => this.props.onClick(i)}
            />
    );
  }

  renderRows(x){
    var row = [];
    for (var i = x*3; i < (x+1)*3; i++) {
        row.push(this.renderSquare(i));
    }
    return <div key={x} className="board-row">{row}</div>;
  }


  render() {
    var rows = [];
    for (var i = 0; i < 3; i++) {
      rows.push(this.renderRows(i));
    }
    return (
      <div>
          {rows}
      </div>
    );
  }
}

class Game extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [
        {
          squares: Array(9).fill(null)
        }
      ],
      moves: Array(9).fill(null),
      stepNumber: 0,
      xIsNext: true
    };
  }

  handleClick(i) {
    const history = this.state.history.slice(0, this.state.stepNumber + 1);
    const current = history[history.length - 1];
    const squares = current.squares.slice();
    const moves   = this.state.moves.slice();
    if (calculateWinner(squares) || squares[i]) {
      return;
    }
    squares[i] = this.state.xIsNext ? "X" : "O";
    // squares: Array(9).fill(null)
    moves[history.length - 1]=i;
    this.setState({
      history: history.concat([
        {
          squares: squares
        }
      ]),
      moves: moves,
      stepNumber: history.length,
      xIsNext: !this.state.xIsNext
    });
  }

  jumpTo(step) {
    this.setState({
      stepNumber: step,
      xIsNext: (step % 2) === 0
    });
  }

  render() {
    const history = this.state.history;
    const current = history[this.state.stepNumber];
    const winner  = calculateWinner(current.squares);
    const move_co = this.state.moves;
    const cur_step    = this.state.stepNumber;
    const moves = history.map((step, move) => {
      const desc = move ?
        'Go to move #' + move + ' ('+( 1 + parseInt(move_co[move-1]/3)) + ',' + ( 1 + parseInt(move_co[move-1]%3)) +')' :
        'Go to game start';
        const btn_class = (move==cur_step) ? 'current':'';
      // const btn_class = step;
      return (
        <li key={move}>
          <button className={btn_class} onClick={() => this.jumpTo(move)}>{desc}</button>
        </li>
      );
    });

    let status;
    if (winner) {
      status = "Winner: " + winner;
    } else {
      status = "Next player: " + (this.state.xIsNext ? "X" : "O");
    }

    return (
      <div className="game">
        <div className="game-board">
          <Board
            squares={current.squares}
            onClick={i => this.handleClick(i)}
          />
        </div>
        <div className="game-info">
          <div>{status}</div>
          <ol>{moves}</ol>
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(<Game />, document.getElementById("root"));

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6]
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}



